body {
    background-color: black;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type=number] {
    -moz-appearance: textfield;
}

:root {
    /*===== Colores =====*/
    --first-color: #FFF;
    --input-color: #80868B  ;
    --border-color: #80868B;

    /*===== Fuente y tipografia =====*/
    --body-font: 'Roboto', sans-serif;
    --normal-font-size: 1rem;
    --small-font-size: .75rem;
}

/*===== BASE =====*/
*, ::before, ::after {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    font-family: var(--body-font);
    font-size: 1rem;
    font-size: var(--normal-font-size);
}

/*===== FORM =====*/
.form__div {
    position: relative;
    height: 48px;
    /*margin-bottom: 1.5rem;*/
}

.form__input {
    position: absolute;
    top: 0;
    left: 0;
    color: #FFFFFF;
    width: 100%;
    height: 100%;
    font-size: 1rem;
    font-size: var(--normal-font-size);
    border: 1px solid #80868B;
    border: 1px solid var(--border-color);
    border-radius: .5rem;
    outline: none;
    padding: 1rem;
    background: none;
    z-index: 1;
}

.form__label {
    position: absolute;
    left: 1rem;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    padding: 0 .25rem;
    background-color: #000;
    color: #80868B;
    color: var(--input-color);
    font-size: 1rem;
    font-size: var(--normal-font-size);
    transition: .3s;
}

/*Input focus move up label*/
.form__input:focus + .form__label {
    top: -.5rem;
    left: .8rem;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    color: #FFF;
    color: var(--first-color);
    font-size: .75rem;
    font-size: var(--small-font-size);
    font-weight: 500;
    z-index: 10;
}

/*Input focus sticky top label*/
.form__input:not(:-ms-input-placeholder).form__input:not(:focus) + .form__label {
    top: -.5rem;
    left: .8rem;
    transform: translateY(0%);
    font-size: .75rem;
    font-size: var(--small-font-size);
    font-weight: 500;
    z-index: 10;
}
.form__input:not(:placeholder-shown).form__input:not(:focus) + .form__label {
    top: -.5rem;
    left: .8rem;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    font-size: .75rem;
    font-size: var(--small-font-size);
    font-weight: 500;
    z-index: 10;
}

/*Input focus*/
.form__input:focus {
    border: 2px solid #FFF;
    border: 2px solid var(--first-color);
}

.input-wrapper {
    width: 100%;
    height: 100%;
    display: inline-block;
}

.delete-icon {
    display: none;
    position: absolute;
    height: 20px;
    right: 0;
}

.item:hover + .delete-icon {
    display: block;
}

.styled-input-wrapper {
    width: 100%;
    display: block;
}

.form-wrapper {
    width: 25vw;
    position: absolute;
    top: 50%;
    left: 50%;
    text-align: center;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.styled-input-wrapper.below{
    margin-top: 20px;
}

.button {
    display: inline;
    border: none;
    padding: 10px 40px;
    background-color: #80868B;
    color: black;
    text-transform: uppercase;
    outline: none;
}

.button:focus {
    outline: none;
}

.button.login {
    margin-top: 40px;
}

